<template>
  <aside class="filter">
    <h2 class="filter__title">Фильтры</h2>

    <form class="filter__form form" action="#" method="get" @submit.prevent="submit">
      <fieldset class="form__block">
      <legend class="form__legend">Цена</legend>
      <label class="form__label form__label--price">
        <input class="form__input" type="text" name="min-price" v-model.number="currentPriceFrom">
        <span class="form__value">От</span>
      </label>
      <label class="form__label form__label--price">
        <input class="form__input" type="text" name="max-price" v-model.number="currentPriceTo">
        <span class="form__value">До</span>
      </label>
      </fieldset>

      <fieldset class="form__block">
      <legend class="form__legend">Категория</legend>
      <label class="form__label form__label--select">
        <select class="form__select" type="text" name="category" v-model.number="currentCategoryId">
          <option value="0">Все категории</option>
          <option v-bind:value="category.id" v-for="category in categories" :key="category.id">{{ category.title }}</option>
        </select>
      </label>
      </fieldset>

      <fieldset class="form__block">
      <legend class="form__legend">Цвет</legend>
        <ul class="colors">
          <li class="colors__item">
          <label class="colors__label">
            <input class="colors__radio sr-only" type="radio" name="color" value="#73B6EA" checked="" v-model="currentColorCheck">
            <span class="colors__value" style="background-color: #73B6EA;">
            </span>
          </label>
          </li>
          <li class="colors__item">
          <label class="colors__label">
            <input class="colors__radio sr-only" type="radio" name="color" value="#FFBE15" v-model="currentColorCheck">
            <span class="colors__value" style="background-color: #FFBE15;">
            </span>
          </label>
          </li>
          <li class="colors__item">
          <label class="colors__label">
            <input class="colors__radio sr-only" type="radio" name="color" value="#939393" v-model="currentColorCheck">
            <span class="colors__value" style="background-color: #939393;">
          </span></label>
          </li>
          <li class="colors__item">
          <label class="colors__label">
            <input class="colors__radio sr-only" type="radio" name="color" value="#8BE000" v-model="currentColorCheck">
            <span class="colors__value" style="background-color: #8BE000;">
          </span></label>
          </li>
          <li class="colors__item">
          <label class="colors__label">
            <input class="colors__radio sr-only" type="radio" name="color" value="#FF6B00" v-model="currentColorCheck">
            <span class="colors__value" style="background-color: #FF6B00;">
          </span></label>
          </li>
          <li class="colors__item">
          <label class="colors__label">
            <input class="colors__radio sr-only" type="radio" name="color" value="#FFF" v-model="currentColorCheck">
            <span class="colors__value" style="background-color: #FFF;">
          </span></label>
          </li>
          <li class="colors__item">
          <label class="colors__label">
            <input class="colors__radio sr-only" type="radio" name="color" value="#000" v-model="currentColorCheck">
            <span class="colors__value" style="background-color: #000;">
          </span></label>
          </li>
        </ul>
      </fieldset>

      <button class="filter__submit button button--primery" type="submit">
        Применить
      </button>
      <button class="filter__reset button button--second" type="button" @click.prevent="reset">
        Сбросить
      </button>
    </form>
  </aside>
</template>

<script>
  import axios from 'axios';
  import {API_BASE_URL} from '../config';

  export default {
    data() {
      return {
        currentPriceFrom: 0,
        currentPriceTo: 0,
        currentCategoryId: 0,
        currentColorCheck: '',

        categoriesData: null
      }
    },
    props: ['priceFrom', 'priceTo', 'categoryId', 'colorCheck'],
    computed: {
      categories() {
        return this.categoriesData ? this.categoriesData.items : [];
      }
    },
    watch: {
      priceFrom(value) {
        this.currentPriceFrom = value;
      },
      priceTo(value) {
        this.currentPriceTo = value;
      },
      categoryId(value) {
        this.currentCategoryId = value;
      },
      colorCheck(value) {
        this.currentColorCheck = value;
      }
    },
    methods: {
      submit() {
        this.$emit('update:priceFrom', this.currentPriceFrom);
        this.$emit('update:priceTo', this.currentPriceTo);
        this.$emit('update:categoryId', this.currentCategoryId);
        this.$emit('update:colorCheck', this.currentColorCheck);
      },
      reset() {
        this.$emit('update:priceFrom', 0);
        this.$emit('update:priceTo', 0);
        this.$emit('update:categoryId', 0);
        this.$emit('update:colorCheck', 0);
      },
      loadCategories() {
        axios.get(API_BASE_URL + '/api/productCategories')
          .then(response => this.categoriesData = response.data);
      }
    },
    created() {
      this.loadCategories();
    }
  }
</script>